import React from 'react';
import icon1 from '../../images/svg-1.svg';
import icon2 from '../../images/svg-2.svg';
import icon3 from '../../images/svg-3.svg';
import consult from '../../images/consult.svg';
import enevelop from '../../images/envelope.svg';
import mgt from '../../images/mgt.svg';
import mobile_app from '../../images/mobile_app.svg';
import pen_tool from '../../images/pen-tool.svg';
import phone from '../../images/phone.svg';
import web from '../../images/web.svg';


import {
    ServicesContainer, ServicesWrapper, ServicesCard, ServicesIcon, ServicesH1, ServicesH2, ServicesP
} from './ServicesElements'

const Services = () => {
    return (
        
        <ServicesContainer id="services">
            <ServicesH1> Our Services </ServicesH1>
            <ServicesWrapper>
                <ServicesCard>
                    <ServicesIcon src={web} />
                    <ServicesH2>Web Development</ServicesH2>
                    <ServicesP>Websites are a vital tool for marketing services and products on the internet, and we happen to be the best at building them at a low cost.</ServicesP>
                </ServicesCard>

                <ServicesCard>
                    <ServicesIcon src={pen_tool} />
                    <ServicesH2>Graphics Design</ServicesH2>
                    <ServicesP>What do you want for your Ethiopian company: a logo, an infographic, a
                            poster, a flyer, or something else entirely? We've got you covered, don't worry.</ServicesP>
                </ServicesCard>

                <ServicesCard>
                    <ServicesIcon src={mobile_app} />
                    <ServicesH2>Mobile App Development</ServicesH2>
                    <ServicesP>We create robust mobile applications. Is it to keep track of your records? Is it a one-of-a-kind creative project? We are confident in our ability to assist you.</ServicesP>
                </ServicesCard>

                
            </ServicesWrapper>
        </ServicesContainer> 
      
    )
}

export default Services
