import React from 'react'

import {
    SidebarContainer, Icon, CloseIcon, SidebarWrapper, SidebarLink, SidebarBtnRoute, SidebarBtnWrap, SidebarMenu
} from './SidebarElements'

const Sidebar = ({isOpen, toggle}) => {
    return (
        <SidebarContainer isOpen={isOpen} onClick={toggle}>
            <Icon onClick={toggle}> 
                <CloseIcon />
            </Icon>
            <SidebarWrapper>
                <SidebarMenu>
                    <SidebarLink to='about' onClick={toggle}> About </SidebarLink>
                    <SidebarLink to='services' onClick={toggle}> Serivices </SidebarLink>
                    <SidebarLink to='discover' onClick={toggle}> Discover </SidebarLink>
                </SidebarMenu>
                <SidebarBtnWrap>
                    <SidebarBtnRoute to="/contact">Contact Us</SidebarBtnRoute>
                </SidebarBtnWrap>
            </SidebarWrapper>
        </SidebarContainer>
    )
}

export default Sidebar
