import React, {useState} from 'react';
import Video from '../../videos/video.mp4';
import { Button } from '../ButtonElements';
import {
    HeroContainer, HeroBg, VideoBg, HeroContent, HeroH1, HeroP, HeroBtnWrapper, ArrowForward, ArrowRight
} from './HeroElements';

const HeroSection = () => {
    const [hover, setHover] = useState(false);

    const onHover = () => {
        setHover(!hover);
    }
    return (
        <HeroContainer>
            <HeroBg>
                <VideoBg autoPlay loop muted src={Video} type="video/mp4"/> 
            </HeroBg>
            <HeroContent>
                <HeroH1>Creative and Efficenent Software Solutions</HeroH1>
                <HeroP>Get a website, Mobile application, and systems for your small or big business Today.</HeroP>
                <HeroBtnWrapper>
                    <Button to='/contact' onMouseEnter={onHover} onMouseLeave={onHover} primary="true">
                        Contact Us {hover ? <ArrowForward /> : <ArrowRight />}
                    </Button>
                </HeroBtnWrapper>
            </HeroContent>
        </HeroContainer>
    )
}

export default HeroSection
